*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Glory', sans-serif;
}
body{
  width: 100vw;
  display: flex;
  justify-content: center;
  background-color: #111;
}
button{
  background-color: #fff;
  padding: 5px 10px;
  font-size: 20px;
  font-weight: bold;
  border-width: 0px;
  cursor: pointer;
}
button:active{
  transform: scale(0.95);
}
div{
  margin: 20px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #fff;
}

div>img{
  border-radius: 50%;
}
